import React from "react";
import ReportTitle from "../common/header-ui/report-title.component";
import ReportFilter from "../common/report-filter.component";
import ReportView from "../common/report-view.component";
import { EtlReportData } from "../types/types";
import { getLocationUuid } from "../utils/utils";
import styles from "./cross-border-report.component.css";
import { fetchCrossBorderReportData } from "./cross-border.resource";
import { useHistory, useLocation } from "react-router-dom";

const CrossBorderReport: React.FC = () => {
  const [loading, setLoading] = React.useState<boolean>(false);
  const [startDate, setStartDate] = React.useState("");
  const [endDate, setEndDate] = React.useState("");
  const [loadReport, setLoadReport] = React.useState<boolean>();
  const [crossBorderReportData, setCrossBorderReportData] = React.useState<
    EtlReportData
  >();
  const history = useHistory();
  const location = useLocation<any>();
  React.useEffect(() => {
    if (endDate && startDate) {
      setLoading(true);
      const ac = new AbortController();
      fetchCrossBorderReportData(
        ac,
        getLocationUuid(),
        endDate,
        startDate
      ).then((resp) => {
        setCrossBorderReportData(resp);
        setLoading(false);
      });
      return () => ac.abort();
    }
  }, [loadReport]);

  React.useEffect(() => {
    if (location?.state) {
      let { startDate, endDate, locationUuid } = location.state;
      if (startDate && endDate && locationUuid) {
        setStartDate(startDate);
        setEndDate(endDate);
        setLoadReport(!loadReport);
      }
    }
  }, []);

  const generateReport = (): void => {
    setLoadReport(!loadReport);
    storeInURL();
  };

  const storeInURL = (): void => {
    const params = {
      startDate: startDate,
      endDate: endDate,
      locationUuid: getLocationUuid(),
    };
    history.push(`/home/cross-border-report`, params);
  };

  return (
    <>
      {!loading ? (
        <div>
          <ReportTitle title="Cross Border Report" />
          <ReportFilter>
            <div className={styles["report-filters"]}>
              <div className={styles.inputContainer}>
                <label htmlFor="startDate">Start Date</label>
                <input
                  type="date"
                  name="startDate"
                  id="startDate"
                  onChange={(evt) => setStartDate(evt.target.value)}
                  value={startDate}
                />
              </div>
              <div className={styles.inputContainer}>
                <label htmlFor="endDate">End Date</label>
                <input
                  type="date"
                  name="endDate"
                  id="endDate"
                  onChange={(evt) => setEndDate(evt.target.value)}
                  value={endDate}
                />
              </div>
            </div>
          </ReportFilter>
          <div className={styles.reportButton}>
            <button
              className="omrs-btn omrs-filled-action"
              onClick={generateReport}
            >
              Generate Report
            </button>
          </div>
          {crossBorderReportData && (
            <ReportView
              indicatorDefinitions={crossBorderReportData.indicatorDefinitions}
              sectionDefinitions={crossBorderReportData.sectionDefinitions}
              result={crossBorderReportData.result}
              queriesAndSchemas={crossBorderReportData.queriesAndSchemas}
              params={{
                patientListUrl: `/home/cross-border-patient-list/${getLocationUuid()}/${startDate}/${endDate}`,
              }}
            />
          )}
        </div>
      ) : (
        <div>Loading...</div>
      )}
    </>
  );
};

export default CrossBorderReport;
