import { FunctionComponent } from "react";
import CrossBorderPatientList from "../cross-border-report/cross-border-patient-list.component";
import CrossBorderReport from "../cross-border-report/cross-border-report.component";
import Home from "../home/home.component";
import OVCPatientList from "../ovc-report/ovc-patient-list.component";
import OVCReport from "../ovc-report/ovc-report.component";

interface Routes {
  name: string;
  path: string;
  component: FunctionComponent | any;
}
export const HIVListsRoutes: Array<Routes> = [
  { name: "home", path: "/home", component: Home },
  { name: "ovc-report", path: "/home/ovc-report", component: OVCReport},
  {
    name: "ovc-patient-list",
    path: "/home/ovc-patient-list/:endDate?/:locationUuids?/:indicators?/:indicatorName?",
    component: OVCPatientList,
  },
  { name: "cross-border-report", path: "/home/cross-border-report", component: CrossBorderReport},
  {
    name: "cross-border-patient-list",
    path: "/home/cross-border-patient-list/:locationUuids?/:startDate?/:endDate?/:indicators?/:indicatorName?",
    component: CrossBorderPatientList,
  },
];
